@import 'susy';
@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,700,700i,900');
@import url('../css/font-awesome.min.css');
@import url("https://cdnjs.cloudflare.com/ajax/libs/ionicons/2.0.1/css/ionicons.min.css");
@font-face {
    font-family: 'sansationbold';
    src: url('../fonts/Sansation-Bold-webfont.eot');
    src: url('../fonts/Sansation-Bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/Sansation-Bold-webfont.woff') format('woff'),
         url('../fonts/Sansation-Bold-webfont.ttf') format('truetype'),
         url('../fonts/Sansation-Bold-webfont.svg#sansationbold') format('svg');
    font-weight: normal;
    font-style: normal;
}
@import 'variables';
@import 'mixins';
@import 'layoutBase';         // Template global : header, menus, pages statiques, footer
@import 'layoutButtons';      // styles boutons
@import 'layoutModals';       // styles fenêtres modales
@import 'layoutProdpages';    // styles pages produits
@import 'layoutEspaceclient'; // styles espace client
